.wrapper {
    display: block;
    position: relative;
    padding: 17px 28px 17px 28px;

    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;

    color: #000000;
}
.label {
    position: absolute;
    top:2px;
    left:28px;

    text-transform: uppercase;
    font-family: SackersGothicStd;
    font-size: 9px;
    line-height: 11px;
    text-align: center;
    letter-spacing: 2px;
}
.input {
    /* reset */
    border:none;
    background-image:none;
    background-color:transparent;
    box-shadow: none;
    outline: none;
    width:100%;

    margin: 0 -10px;
    padding: 3px 10px;
    border-bottom: 1px solid #E7E7E7;
    transition: border-color .2s;

    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
}
.input:focus {
    border-color: #000;
}
