.page {
    height: 100%;
    position: relative;
    display:flex;
    flex-direction: column;
    background: #fff;
}
.backArrow {
    position: absolute;
    top: 8px;
    left: 13px;
}
.title {
    padding-top: 38px;
    padding-bottom: 20px;
    flex-grow: 0;
}
.content {
    flex-grow: 1;
    overflow: auto;
    position: relative;

    padding-top: 50px;
}