.button {
    /* reset */
    border:none;
    background-image:none;
    background-color:transparent;
    box-shadow: none;
    outline: none;

    padding: 19px 92px;
    
    text-transform: uppercase;
    font-family: SackersGothicStd;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 2px;

    color: #FFFFFF;
    background: #000000;
}

.round {
    border-radius: 50%;
}