@import-normalize;

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: "Montserrat",
    -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:global(#root) {
  height:100%;
}

* {
  box-sizing: border-box;
}

:global(.transition-wrapper) {
  height:100%;
  position: relative;
}

:global(.transition-item.transition-item) {
  position: absolute;
  top:0; left:0; right:0; bottom:0;
  transition: transform .4s cubic-bezier(.1, .7, .1, 1), opacity .4s cubic-bezier(.1, .7, .1, 1);
}
:global(.topLevelPage) {
  z-index:1
}
:global(.lowLevelPage) {
  z-index:2
}
:global(.lowLevelPage.transition-appear) {
  transform: translate3d(100%, 0, 0);

}
:global(.lowLevelPage.transition-appear.transition-appear-active) {
  transform: translate3d(0, 0, 0);
}
:global(.lowLevelPage.transition-leave) {
  transform: translate3d(0, 0, 0);

}
:global(.lowLevelPage.transition-leave.transition-leave-active) {
  transform: translate3d(100%, 0, 0);
}

:global(.transition-item.topLevelPage) {
  transition: transform .4s cubic-bezier(.1, .7, .1, 1);
}
:global(.transition-item.topLevelPage:after) {
  content: '';
  position: absolute;
  top: 0;left: 0;right: 0;bottom: 0;
  background: #000;
  opacity: 0;
  transition: opacity .4s cubic-bezier(.1, .7, .1, 1);
}

:global(.topLevelPage.transition-appear) {
  transform: translate3d(-50%, 0, 0);
}
:global(.topLevelPage.transition-appear:after) {
  opacity: 0.6;
}
:global(.topLevelPage.transition-appear-active) {
  transform: translate3d(0, 0, 0);
}
:global(.topLevelPage.transition-appear-active:after) {
  opacity: 0;
}

:global(.topLevelPage.transition-leave) {
  transform: translate3d(0, 0, 0);
}
:global(.topLevelPage.transition-leave:after) {
  opacity: 0;
}
:global(.topLevelPage.transition-leave-active) {
  transform: translate3d(-50%, 0, 0);
}
:global(.topLevelPage.transition-leave-active:after) {
  opacity: 0.6;
}